<template>
    <div>
        <!-- 详情页尾部 -->
        <div class="blank"></div>
        <div class="footer">
            <div class="bookroom" @click="bookroom">
                <span></span>
                <p>前往书库</p>
            </div>
            <div class="collection" @click="collection">
                <p v-show="$store.state.books.details_view[$store.state.books.details_id].iscollection">收藏</p>
                <p class="active">已收藏</p>
            </div>
            <div class="read" @click="read">
                <span>开始阅读</span>
            </div>
        </div>
    </div>
</template>
<script>
import Vue from 'vue';
import { Toast } from 'vant';
Vue.use(Toast);
export default {
    data(){
        return{
           
        }
    },
    methods:{
        collection(){
            if(this.$store.state.user.islogin==true){
                if(this.$store.state.books.details_view[this.$store.state.books.details_id].iscollection==true){
                Toast('漫画已收藏');
                this.$store.commit('books/isadd');
            }else{
                this.$store.commit('books/isadd');
                Toast('漫画已取消收藏');
            }
            }else{
                this.$router.push({path:"/login"})
            }
            
        },
        bookroom(){
            this.$router.push({path:"/index"});
        },
        read(){
            if(this.$store.state.user.islogin==true){
            this.$store.commit('books/read')
            this.$router.push({path:"/read"});
            }else{
                this.$router.push({path:"/login"})
            }
        }
    }
}
</script>
<style lang="scss" scoped>
.blank{
    height:3.5rem;
}
    .footer{
        display:flex;
        position: fixed;
        bottom:0;
        width:100%;
        height:3.25rem;
        border-top:1px solid #c5c5c5;
        background-color:white;
        .bookroom{
            width:20%;
             margin-top:0.725rem;
             margin-left:1rem;
            span{
                display:block;
                width:1.125rem;
                height:1.125rem;
                background-image:url(../../assets/sc_img-index.png);
                background-size: 4.025rem 3.275rem;
                background-position:-1.6rem -1.925rem;
                background-repeat: no-repeat;
                margin:0 auto;
            }
            p{
                text-align:center;
                margin:0;
                color:#333;
                font-size:0.4rem;
                width:100%;
            }
        }
        .collection{
            width:25%;
            p{
                font-size:0.7rem;
                font-weight:bold;
                width:85%;
                height:1.8rem;
                background-color:#f4f4f4;
                border-radius:1rem;
                color:#333;
                text-align:center;
                line-height:1.8rem;
                margin-left:15%;
                margin-top:0.725rem;
            }
            .active{
                color:white;
                background-color:#ff613e;
            }
        }
        .read{
            width:55%;
            span{
                display:block;
                font-size:0.7rem;
                width:80%;
                height:1.8rem;
                line-height:1.8rem;
                background-color:#ff613e;
                border-radius:0.875rem;
                text-align: center;
                font-weight:bold;
                color:white;
                margin-top:0.725rem;
                margin-left:15%;
            }
        }
    }
    
</style>