<template>
<!-- 详情页漫画信息部分 -->
    <div class="dateils">
        <van-tabs>
            <van-tab title="详情">
                <div class="content">
                    <span></span>
                    <p>{{$store.state.books.details_view[$store.state.books.details_id].obj.item.details}}</p>
                </div>
            </van-tab>
            <van-tab title="目录">
                <div class="catalog">
                    <ul>
                    <li @click="read">1</li>
                    <li @click="second">2
                        <span>(付费)</span>
                    </li>
                    <li @click="third">3
                        <span>(付费)</span>
                    </li>
                   
                </ul>
                </div>
            </van-tab>
        </van-tabs>
        
    </div>
</template>
<script>
import Vue from 'vue';
import { Dialog } from 'vant';
Vue.use(Dialog);
export default {
    watch:{
        // 进入第几话
      "$store.state.books.read_id":function(){    
                this.$router.push({path:"/read"});
        }
    },
    methods:{
        read(){
            if(this.$store.state.user.islogin==true){
                this.$router.push({path:"/read"});
            }else{
                this.$router.push({path:"/login"});
            }
            
        },
        second(){
            console.log(this.$store.state.books.details_view[this.$store.state.books.details_id].obj.item.second)
             if(this.$store.state.user.islogin==true){
                if(this.$store.state.books.details_view[this.$store.state.books.details_id].obj.item.second==false){
                    if(this.$store.state.user.user[this.$store.state.user.userID].coupons>=10){
                        this.$store.commit('books/seconds_chapter');
                        this.$store.commit('user/second_payment');
                    }else{
                        Dialog.confirm({
                        title: '提示',
                        message: '您的余额不足，是否去充值'
                        }).then(() => {
                        this.$router.push({path:"/payment"})
                        }).catch(() => {
                        Dialog.close();
                        });
                    }
                }else{
                    this.$store.commit('books/seconds_chapter');
                }
               
            }else{
                this.$router.push({path:"/login"});
            }
            
        },
        third(){
            if(this.$store.state.user.islogin==true){
               if(this.$store.state.books.details_view[this.$store.state.books.details_id].obj.item.third==false){
                    if(this.$store.state.user.user[this.$store.state.user.userID].coupons>=10){
                        this.$store.commit('books/third_chapter');
                        this.$store.commit('user/third_payment');
                    }else{
                        Dialog.confirm({
                        title: '提示',
                        message: '您的余额不足，是否去充值'
                        }).then(() => {
                        this.$router.push({path:"/payment"})
                        }).catch(() => {
                        Dialog.close();
                        });
                    }
                }else{
                    this.$store.commit('books/third_chapter');
                }
            }else{
                this.$router.push({path:"/login"});
            }
        },
    }
}
</script>
<style lang="scss" scoped>
    ::v-deep .van-tabs__line{
        background-color: #ff9a6a;
        width: 0.5rem !important;
        height: 0.15rem;
    }
    ::v-deep .van-tab{
        font-size:0.75rem;
        color:#535252;
    }
    .content{
        span{
            display:block;
            width:2.35rem;
            height:1.95rem;
            background-image: url(../../assets/details_img.png);
            background-position: 0 0;
            margin:0.5rem auto;
        }
        p{
            padding:0 1.25rem;
            font-size: 0.7rem;
            color: #535252;
            padding-bottom: 0.75rem;
        }
    }
    .catalog{
        ul{
            display:flex;
            flex-wrap:wrap;
            list-style: none;
            li{
                width:20%;
                border:1px solid #c5c5c5;
                height:2.25rem;
                line-height: 2.25rem;
                text-align: center;
                color:#535252;
                font-size:0.75rem;
                padding:0 4%;
                margin-left:4%;
                margin-top:1.6rem;
                span{
                    color:red;
                    font-size:0.75rem;
                }

            }
        }
    }
</style>